img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "**";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/**.woff2") format("woff2"), url("../fonts/**.woff") format("woff");
}
@font-face {
  font-family: "**";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/**.woff2") format("woff2"), url("../fonts/**.woff") format("woff");
}